#dv-footer {
  --footer-center-width: 0px;
  --footer-height: 60px;
  --footer-side-width: calc(100vw - var(--footer-center-width));
  --footer-item-width: calc(var(--footer-side-width) * 0.20);
  --footer-indicator-height: 2px;
}

#dv-footer {
  width: calc(100vw);
  height: var(--footer-height);
  background: transparent;
  position: fixed;
  bottom: 0;
  /*border-top-left-radius: 10px;
  border-top-right-radius: 10px;*/
  border-top: 1px solid #e6e6e6;
  background: #f2f2f2;
  display: flex;
}

#dv-footer .frame {
  width: calc(var(--footer-side-width) * 0.5);
  height: var(--footer-height);
  position: relative;
  bottom: 0px
}

#dv-footer .left {
  /*background-image: url('resources/side.png');*/
}

#dv-footer .center {
  visibility: hidden;
  width: var(--footer-center-width);
  /*background-size: 100% 85px;
  background-image: url('resources/center.png');*/
}

#dv-footer .right {
  /*background-image: url('resources/side.png');*/
}

#dv-footer .button {
  background-color: cadetblue;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: relative;
  left: 15px;
  bottom: 22px;
  text-align: center;        
  box-shadow: 2px 3px 1px 2px #00000061;
}

#dv-footer .button a {
  line-height: 50px;
  color: white;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  text-shadow: 1px 1px 2px #00000061;
}

#dv-footer .bottom-button {    
  text-decoration: none;
  line-height: 60px;
  width: var(--footer-item-width);
  font-size: 34px;
  text-align: center;
  color: transparent;
  -webkit-tap-highlight-color: transparent;
  -webkit-text-stroke: 1px #4f96c2;
}

#dv-footer .bottom-button.selected {
  color: #4f96c2;
  background-color: #4f96c233;
}

#dv-footer ul {
  padding-left:0px;
  margin: none;
  width: 100%;
  height: 100%;
  margin: 0;
}

#dv-footer li{
  padding: 0;
  width: 20%;
}

#dv-footer.row{
  margin: 0;
}

#dv-footer div.indicator {
  /*width: var(--footer-item-width);*/
  width: 100%;
  position: absolute;
  /*background: #4f96c2;*/
  height: var(--footer-indicator-height);
  bottom: 0px;
}

#dv-indicator {
  width: var(--footer-item-width);
  height: 5px;  
  display: inline-flex;
  position: absolute;
  bottom: 0px;
  /*background: #4f96c2;*/
}

#dv-left {
  position: absolute;
  width: var(--footer-item-width);
  left: 0px;
  height: var(--footer-indicator-height);
  background: #4f96c2;
}