#dv-tour-detail img {
  max-width: 100%;
}

#dv-tour-detail li.tab{
  list-style: none;
  max-width: 100%;
  height: var(--tour-detail-header-height);
  line-height: var(--tour-detail-header-height);
}

#dv-tour-detail ul.tabs {
  height: var(--tour-detail-header-height);
}

#dv-tour-detail {
  margin-top: var(--header-height);
  width: 100vw;
  overflow: hidden;   
  position: absolute;
  top: 0;
  background-color: white;
  
  border-top-left-radius: 40px;
  border-top-right-radius: 40px;
  box-shadow: 0px -20px 20px 0px #0000002e;
}

#dv-tour-detail-tab {
  /*margin-bottom: 50px;*/
}

#dv-tour-detail-tab span {
  display: inline-block;
  background-color: white;
  /*background: linear-gradient(0deg, rgba(245,245,245,1) 0%, rgba(209,209,209,1) 100%);*/
  width: calc(100vw * 0.3333);
  text-align: center;
  line-height: 50px;
  color: transparent;
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
  position:relative;
}

#dv-tour-detail-tab span div {
  color: #4f96c2;
}

#dv-tour-detail-tab span.on div {
  color: #4f96c2;
  font-weight: bold;
}

#dv-tour-detail-tab span.on{
  background: #4f96c233;
  color: #4f96c233;
}
