#dv-tour-detail-tab .line {
  width: 1px;
  display: inline-block;
}

#dv-tour-detail .nodisplay {
  display: none;
}

#dv-tour-detail-body {    
  width: 100vw;
  display: inline-flex;
  position: relative;
  height: calc(100vh - var(--tour-detail-header-height) - var(--header-height));
  overflow-x: hidden;
  overflow-y: hidden; 
  /*scroll-snap-type: x mandatory;*/
}

#dv-tour-detail-body .inner-box {
  display: block;
  height: calc(100vh - var(--tour-detail-header-height) - var(--header-height));
  width: 100vw;
  overflow-x: hidden;
  overflow-y: auto;
  scroll-snap-align: start;
  position: absolute;
}

#dv-tour-detail-info {
  left: 100vw;
}

#dv-tour-detail-gallery {
  left: 200vw;
}

#dv-tour-detail-body .tabs-content.carousel-slider{
  height: 100% !important;
}

#dv-tour-detail-body .inner-body {
  padding-top: 50px;
  width: 90%;
  margin-left: 5%;
  padding-bottom: 50px;
}

#dv-tour-detail-gallery-list {
  overflow-x: auto;
  width: 100%;
}
#dv-tour-detail-gallery-list .list {
  display: inline-flex;
}

#dv-tour-detail-gallery-list .item {
  width: 100px;
  height: 60px;
  /* object-fit: contain; */
  overflow: hidden;
  border: 3px solid transparent;
  /* flex-direction: row; */
  justify-content: center;
  display: flex;
  flex-direction: row;
}

#dv-tour-detail-gallery-list .item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
#dv-tour-detail-gallery-list .item.selected {
  border: 3px solid #ff5722;;
}

#dv-tour-detail .row .col.s12 {
  padding: 0;
}

#dv-tour-detail .tabs .tab a.active,
#dv-tour-detail .tabs .tab a:hover {
  color: #4f96c2;
}

#dv-tour-detail .tabs .tab a {
  color: #4f96c2a6;
}

#dv-tour-detail .tabs .tab a:focus, 
#dv-tour-detail .tabs .tab a:focus.active {
  background-color: #4f96c233;
}

#dv-tour-detail .tabs .indicator {
  background-color: #4f96c2;
}

#dv-indicator-bar{
  background-color: #4f96c2;
  height: 2px;
  width: calc(100vw / 3);
  position: absolute;
}