#dv-body {
  /*height: calc(100vh - 60px);*/
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: absolute;
  top: 0px;
  /*padding-bottom: 60px;*/
  /*padding-top: 10px;*/
}

#dv-body-back {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0px;
  opacity: 0;
  -webkit-transition: 1.0s ease;
  -moz-transition: 1.0s ease;
  -o-transition: 1.0s ease;
  transition: 1.0s ease;
}
body {
  background-color: #efefef;
}

iframe {
  width: 100vw;
  height: 100vh;
}

.transition-none {
  -webkit-transition: none;
  -moz-transition: none;
  -o-transition: none;
  transition: none;
}

.transition-default {
  -webkit-transition: .5s ease;
  -moz-transition: .5s ease;
  -o-transition: .5s ease;
  transition: .5s ease;
}

.transition-fast {
  -webkit-transition: .35s ease;
  -moz-transition: .35s ease;
  -o-transition: .35s ease;
  transition: .35s ease;
}
.transition-x2 {
  -webkit-transition: .25s ease;
  -moz-transition: .25s ease;
  -o-transition: .25s ease;
  transition: .25s ease;
}