#dv-main-buttons {
  display: flex;
}

#dv-main-buttons .dv-button {
  width: 26vw;
  height: calc(26vw * 1.2);
  margin-left: 16vw;
  margin-bottom: 20px;
  background-color: white;
  color: black;
  font-size: 18px;
  font-weight: bold;
  border-radius: 3px;
  box-shadow: 7px 7px 3px 3px #0000002e;
}

#dv-main-buttons .dv-button span {
  width: 16vw;
  height: 16vw;
  margin-left: 5vw;
  margin-top: 2vw;
  background-color: black;
  /*box-shadow: 8px 8px 5px 0px #0000002e;*/
  border-radius: 50%;
  /*color: white;*/
  font-size: 11vw;
  line-height: 16vw;
  margin-bottom: 5vw;

  color: transparent;
  -webkit-text-stroke: 2px white;
}

.dv-button span.gradient-1 {
  background: #fb9181;  /* fallback for old browsers */
  background: -webkit-linear-gradient(225deg, #fb9181 0%, #f3d8ab);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(225deg, #fb9181 0%, #f3d8ab); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.dv-button span.gradient-2 {
  background: #8cd6c5;  /* fallback for old browsers */
  background: -webkit-linear-gradient(225deg, #c4afce 0%, #8cd6c5);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(225deg, #c4afce 0%, #8cd6c5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.dv-button span.gradient-3 {
  background: #0eeced;  /* fallback for old browsers */
  background: -webkit-linear-gradient(225deg, #0eeced 0%, #3bafff);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(225deg, #0eeced 0%, #3bafff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.dv-button span.gradient-4 {
  background: #9862d1;  /* fallback for old browsers */
  background: -webkit-linear-gradient(225deg, #e47cad 0%, #9862d1);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(225deg, #e47cad 0%, #9862d1); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
 /*main*/
#dv-body-back.gradient-1 {
  opacity: 1;
  background: #bcecf8;  /* fallback for old browsers */
  background: -webkit-linear-gradient(230deg, #f2faeb 12%, #bcecf8);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(230deg, #f2faeb 12%, #bcecf8); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
 /*game*/
#dv-body-back.gradient-2 {
  background: #9862d1;  /* fallback for old browsers */
  background: -webkit-linear-gradient(225deg, #ffcae3 12%, #bc7dff);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(225deg, #ffcae3 12%, #bc7dff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
/*tour*/
#dv-body-back.gradient-3{
  background: #8cd6c5;  /* fallback for old browsers */
  background: -webkit-linear-gradient(225deg, #f1d3ff  0%, #b9fbec 50%);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(225deg, #f1d3ff  0%, #b9fbec 50%); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
