#dv-logo-container {
  width: 100%;
  height: 100%;
}

#dv-body-back{
  width: 100%;
  height: 100%;
}

#logo-0 {
  background: #b8eba8;  /* fallback for old browsers */
  background: -webkit-linear-gradient(225deg, #b8eba8 0%, #78d2ec);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(225deg, #b8eba8 0%, #78d2ec); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

#logo-1 {
  background: #9862d1;  /* fallback for old browsers */
  background: -webkit-linear-gradient(225deg, #e47cad 0%, #9862d1);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(225deg, #e47cad 0%, #9862d1); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

#logo-2 {
  background: #8cd6c5;  /* fallback for old browsers */
  background: -webkit-linear-gradient(225deg, #c4afce 0%, #8cd6c5);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(225deg, #c4afce 0%, #8cd6c5);
}

.logo {
  border-radius: 50%;
  width: 100px;
  height: 100px;
  border: solid 7px white;
  display: flex;
  justify-content: center;
  flex-direction: column;
  box-shadow: 5px 5px 10px 2px #0000002e;
  margin-left: calc(50vw - 57px);
  position: absolute;
  top: 50px;
  -moz-box-sizing: unset;
  -webkit-box-sizing: unset;
  box-sizing: unset;
}

.logo.large{
  top: calc(50vh - 100px) !important;
  transform: scale(1.5) !important;
}

.logo.small {
  top: 50px;
  transform: scale(1);
}

.logo img{
  width: 96%;
  margin-left: 2%;
  position: relative;
  top: -5px;
}

#dv-logo {
  line-height: 100%;
  font-size: 24px;
  text-align: center;
  display: flex;  
  justify-content: center;
  flex-direction: column;
}

#dv-logo .material-icons {
  font-size: 80px;
  color: transparent;
  -webkit-tap-highlight-color: transparent;
  -webkit-text-stroke: 2px #ffffff;
}

#dv-logo span {
  width: 100%;
  position: absolute;
}

#dv-logo div{
  text-align: center;
  display: flex;
  justify-content: center;
  width: 100px;
  height: 70px;
  flex-direction: column;
  top: 18px;
  position: relative;
}

#dv-logo .logo-title {
  position: relative;
  color: white;
  font-weight: bold;
  text-shadow: 0px 0px 6px black;
  bottom: 0px;
  word-break: keep-all;
}

#dv-logo-fullscreen {
  background-color: transparent;
  width: 100vw;
  height: 100vh;
  position: relative;
}

