.category.icon {
  width: 45px;
  height: 45px;
  border: 0px;
  border-radius: 50%;
  display: table;
  margin: 0px;
  position: relative;
  top: 8.5px;
  color: transparent;
  -webkit-tap-highlight-color: transparent;
  -webkit-text-stroke: 2px white;
  font-size: 37px;
  text-align: center;
  line-height: 45px;
}