#indicator {
  width: 100px;
  height: 100px;
  position: absolute;
    top: calc(50% - 50px);
    left: calc(50% - 50px);
  animation: spin 1s linear infinite;
}

#circle {
  fill: none;
  stroke: url(#gradient);
  /*stroke: linear-gradient(45deg, #cf3856 0%, #f46638 100%);*/
  /*stroke: rgba(255,255,255,0.5);*/
  stroke-linecap: round;
  stroke-width: 10;
  animation: draw 3s ease-in-out infinite;
}

#circle-back {
  fill: none;
  stroke-linecap: round;
  stroke-width: 10;
  stroke: #efefef;
}

@keyframes draw {
  0% { stroke-dasharray: 20, 282.6;}
  50% { stroke-dasharray: 200, 282.6; }
  100% { stroke-dasharray: 20, 282.6; }
}

@keyframes spin {
  to { transform: rotate(360deg); }
}