.tour-img-small {
  /*width: 90px;*/
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: solid #d8d8d8 1px;
  display: flex;
  margin: 0px;
  position: relative;
  top: 8.5px;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.tour-img-small img{  
  width: 200%;
}
#dv-tour-arrow {
  position: relative;
  left: unset;
  right: -17px;
  border-radius: 50%;
  background-color: white;
  width: 35px;
  height: 35px;
  display: inline-table;
  top: 0px;
  box-shadow: 3px 3px 8px 0px #0000002e;
}

#dv-tour-item {
  width: calc(80% - 20px);
  height: 64px;
  background-color: white;
  border-radius: 10px;
  /*border-bottom: solid #d8d8d8 1px;*/
  margin-bottom: 25px;
  margin-left: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: unset;
  text-decoration: unset;
  padding-left: 10px;
  box-shadow: 3px 3px 8px 0px #0000002e;
}

.tour-arrow {
  width: 14px;
  height: 14px;
  border-top: solid #7a7a7a 4px;
  border-right: solid #7a7a7a 4px;
  transform: rotate(45deg);
  display: flex;  
  position: relative;
  top: 9px;
  left: 6px;
}

#dv-tour-item .tour-item {
  width: calc(100vw - 60px);
  display: flex;
  height: 64px;
}

#dv-tour-item .tour-item p{
  padding: 0px;
  margin: 0px;
  vertical-align: middle;
  text-align: center;
  width: 100%;
  font-size: 20px;
  font-weight: bold;
  line-height: 64px;
}

