#dv-tour {
  height: 100vh;
  height: 100%;    
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
}

#dv-tour-list {
  position: absolute;
  width: 100%;
  height: calc(100% - 150px - 70px);
  bottom: 0px;
}

#dv-tour-list{

}

#dv-tour-list .scroll-view {
  display: flex;
  position: absolute;
  left: 0px;
  bottom: 0px;

  background-color: white;
  border-top-left-radius: 40px;
  border-top-right-radius: 40px;
  /*box-shadow: 0px -20px 20px 0px #0000002e;*/
  padding-top: 40px;
  width: 50%;
  height: 100%;
}

#dv-tour-list .container {
  overflow-y: auto;
}

.items .container{
  margin-top: 40px;
}

#dv-tour-list .contents {
  /*height: 100%;
  overflow-y: auto;*/
}

#dv-tour-list .items {
  left: 100vw;
}

#dv-tour-list .anchor {
  left: 0;
  position: relative;
  height: calc(100% - 40px);
  width: 200%;
}

#dv-tour-list .button-back {
  top: 12px;
  left: 12px;
  position: absolute;
  font-size: 50px;
  background-color: white;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  /*box-shadow: 3px 3px 20px 0px #0000002e;*/
}