#dv-poi {
  position: absolute;
  width: 100%;
  /*height: calc(100% - 215px - 40px);*/
  height: calc(100%);
  
  background-color: transparent;
  border-top-left-radius: 40px;
  border-top-right-radius: 40px;
  /*box-shadow: 0px -20px 20px 0px #0000002e;*/
  /*padding-top: 40px;*/
  position: absolute;
  bottom: 0px;
}

#dv-poi.containder {
  overflow: hidden;
}

#dv-poi #labels {
  position: absolute;  /* 캔버스 위에 자리잡도록 합니다. */
  left: 0;             /* id=container 위 왼쪽에 기준하도록 합니다. */
  top: 0;
  color: white;
  visibility: hidden;
}

#dv-poi #labels > div {
  -webkit-transition: .1s ease;
  -moz-transition: .1s ease;
  -o-transition: .1s ease;
  transition: .1s ease;

  position: absolute;  /* 캔버스 위에 자리잡도록 합니다.  */
  left: 0;             /* 부모의 위 왼쪽에 기준하도록 합니다. */
  top: 0;
  cursor: pointer;     /* 포인터가 위에 올라갔을 때 포인터 스타일을 바꿉니다. */
  font-size: large;
  user-select: none;   /* 텍스트를 선택할 수 없도록 합니다. */
  word-break: keep-all;
  background-color: #ffffffbb;
  border-radius: 10px;
  white-space: pre;
  padding: 10px;
  text-shadow:
    -1px -1px 0 #000,
     0   -1px 0 #000,
     1px -1px 0 #000,
     1px  0   0 #000,
     1px  1px 0 #000,
     0    1px 0 #000,
    -1px  1px 0 #000,
    -1px  0   0 #000;
}

#dv-poi #labels > div:hover {
  color: red;
}

#dv-poi #labels > div:after{
  content: "";
  border-top: 8px solid #ffffffbb;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  border-bottom: 0px solid transparent;
  bottom: -8px;
  left: calc(50% - 4px);
  position: absolute;
}